import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';

// import Google Charts
// import {GoogleCharts} from 'google-charts';

import Main from './panels/Main';
import Entry from './panels/Entry';
import Account from './panels/Account';
import { ACTIVE_DELAY } from '@vkontakte/vkui/dist/components/Tappable/Tappable';
import { ConfigProvider } from '@vkontakte/vkui';
// import Persik from './panels/Persik';


const App = () => {
	const [activePanel, setActivePanel] = useState('entry');
	const [history, setHistory] = useState(['main']);
	// const [fetchedUser, setUser] = useState(null);

	// useEffect(() => {
	// 	bridge.subscribe(({ detail: { type, data }}) => {
	// 		if (type === 'VKWebAppUpdateConfig') {
	// 			const schemeAttribute = document.createAttribute('scheme');
	// 			schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
	// 			document.body.attributes.setNamedItem(schemeAttribute);
	// 		}
	// 	});
	// 	async function fetchData() {
	// 		const user = await bridge.send('VKWebAppGetUserInfo');
	// 		setUser(user);
	// 	}
	// 	fetchData();

	// }, []);

	// Swipe back
	const  goBack = () => {
		history.pop();
		activePanel = history[history.length - 1];
		if (activePanel === 'main') {
			bridge.send('VKWebAppDisableSwipeBack');
		}
		setHistory(history);
		setActivePanel(activePanel);
	} 

	const go = e => {
		const panel_to = e.currentTarget.dataset.to;
		if (panel_to === 'main' && activePanel !== 'entry') {
			history.pop()
		}
		else {
			history.push(e.currentTarget.dataset.to);
		}
		setActivePanel(e.currentTarget.dataset.to);
		setHistory(history);
	};

	return (
		<ConfigProvider isWebView={true}>
			<View activePanel={activePanel} >
				<Entry id='entry' go={go} onSwipeBack={goBack} />
				<Main id='main' go={go} onSwipeBack={goBack}/>
				{/* <Persik id='persik' go={go} /> */}
				<Account id='account' go={go} onSwipeBack={goBack}/>
			</View>
		</ConfigProvider>
	);
}

export default App;

